import React, { Suspense } from 'react';
import { BrowserRouter as Router, Routes, Route, useLocation } from 'react-router-dom';
import { AnimatePresence, motion } from 'framer-motion';
import Navbar from './components/Navigation/Navbar';
import Footer from './components/Navigation/Footer';
import PlayerDetail from './pages/PlayerDetail';


import { useState, useEffect, useRef } from 'react';
import { Volume2, VolumeX } from 'lucide-react';


const BackgroundMusic = () => {
  const [isPlaying, setIsPlaying] = useState(false);
  const [hasInteracted, setHasInteracted] = useState(false);
  const audioRef = useRef(new Audio('/sweetcaroline.mp4')); 

  const startMusic = async () => {
    if (!hasInteracted) {
      try {
        audioRef.current.loop = true;
        await audioRef.current.play();
        setIsPlaying(true);
        setHasInteracted(true);
      } catch (error) {
        console.error('Audio playback failed:', error);
      }
    }
  };

  const toggleAudio = (e) => {
    e.stopPropagation(); 
    if (isPlaying) {
      audioRef.current.pause();
    } else {
      audioRef.current.play();
    }
    setIsPlaying(!isPlaying);
  };

  return (
    <>
      {!hasInteracted && (
        <div 
          onClick={startMusic}
          className="fixed inset-0 z-50 cursor-pointer bg-transparent"
          aria-label="Click anywhere to start music"
        >
          <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 text-gray-800 text-xl font-medium">
          </div>
        </div>
      )}
      
      {hasInteracted && (
        <button
          onClick={toggleAudio}
          className="fixed bottom-4 right-4 z-50 p-3 bg-white rounded-full shadow-lg hover:bg-gray-100 transition-colors"
          aria-label={isPlaying ? 'Mute music' : 'Unmute music'}
        >
          {isPlaying ? (
            <Volume2 className="w-6 h-6 text-gray-800" />
          ) : (
            <VolumeX className="w-6 h-6 text-gray-800" />
          )}
        </button>
      )}
    </>
  );
};


const Home = React.lazy(() => import('./pages/Home'));
const About = React.lazy(() => import('./pages/About'));
const Players = React.lazy(() => import('./pages/Players'));
const Schedule = React.lazy(() => import('./pages/Schedule'));

const LoadingSpinner = () => (
  <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-[#003057]"></div>
);

const PageTransition = ({ children }) => {
  const pageVariants = {
    initial: { opacity: 0, y: 20 },
    animate: { opacity: 1, y: 0 },
    exit: { opacity: 0, y: -20 }
  };

  return (
    <motion.div
      initial="initial"
      animate="animate"
      exit="exit"
      variants={pageVariants}
      transition={{ duration: 0.3 }}
    >
      {children}
    </motion.div>
  );
};

const ScrollToTop = () => {
  const { pathname } = useLocation();
  
  React.useEffect(() => {
    window.scrollTo({
      top: 0,
      behavior: pathname === '/' ? 'auto' : 'smooth'
    });
  }, [pathname]);

  return null;
};

const AppContent = () => {
  const location = useLocation();

  return (
    <div className="flex flex-col min-h-screen bg-gray-50">
      <Navbar />
      <main className="flex-grow">
        <BackgroundMusic /> 
        <AnimatePresence mode="wait">
          <Suspense 
            fallback={
              <div className="flex items-center justify-center min-h-[60vh]">
                <LoadingSpinner />
              </div>
            }
          >
            <PageTransition key={location.pathname}>
              <Routes location={location}>
                <Route path="/" element={<Home />} />
                <Route path="/about" element={<About />} />
                <Route path="/players" element={<Players />} />
                <Route path="/schedule" element={<Schedule />} />
                <Route path="/players/:playerName" element={<PlayerDetail />} />
                <Route path="*" element={
                  <div className="flex flex-col items-center justify-center min-h-[60vh] space-y-4">
                    <h1 className="text-4xl font-bold text-gray-800">404</h1>
                    <p className="text-gray-600">Page not found</p>
                  </div>
                } />
              </Routes>
            </PageTransition>
          </Suspense>
        </AnimatePresence>
      </main>
      <Footer />
    </div>
  );
};

const App = () => {
  return (
    <Router>
      <ScrollToTop />
      <AppContent />
    </Router>
  );
};

export default App;