import React from 'react';
import { useParams, useLocation, useNavigate } from 'react-router-dom';
import { ArrowLeft, User } from 'lucide-react';

const PlayerDetail = () => {
  const { playerName } = useParams();
  const location = useLocation();
  const navigate = useNavigate();
  const playerData = location.state?.playerData;

  // If no player data was passed through navigation state, 
  // we could fetch it here based on the playerName parameter

  if (!playerData) {
    return (
      <div className="min-h-screen bg-gray-50 flex flex-col items-center justify-center p-4">
        <User className="w-16 h-16 text-gray-400 mb-4" />
        <h1 className="text-2xl font-bold text-gray-800 mb-2">Player Not Found</h1>
        <p className="text-gray-600 mb-4">The player you're looking for doesn't exist or has been removed.</p>
        <button
          onClick={() => navigate('/players')}
          className="flex items-center gap-2 px-4 py-2 bg-[#003057] text-white rounded-lg hover:bg-[#002442] transition-colors"
        >
          <ArrowLeft className="w-4 h-4" />
          Back to Roster
        </button>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Hero Section */}
      <section className="bg-[#003057] relative overflow-hidden">
        <div className="absolute inset-0 bg-grid-white/5" />
        <div className="container mx-auto px-4 py-6 relative">
          <button
            onClick={() => navigate('/players')}
            className="flex items-center gap-2 text-white/80 hover:text-white transition-colors mb-6"
          >
            <ArrowLeft className="w-4 h-4" />
            Back to Roster
          </button>
        </div>
      </section>

      {/* Player Info */}
      <div className="container mx-auto px-4 mt-24 relative z-10">
        <div className="bg-white rounded-xl shadow-xl overflow-hidden">
          <div className="md:flex">
            {/* Player Image */}
            <div className="md:w-1/3">
              {playerData.img ? (
                <img
                  src={playerData.img}
                  alt={playerData.name}
                  className="w-full h-full object-cover aspect-[3/4]"
                />
              ) : (
                <div className="w-full h-full aspect-[3/4] bg-gray-100 flex items-center justify-center">
                  <User className="w-20 h-20 text-gray-400" />
                </div>
              )}
            </div>

            {/* Player Details */}
            <div className="p-6 md:p-8 md:w-2/3">
              <div className="mb-6">
                <div className="flex items-start justify-between gap-4 mb-4">
                  <div>
                    <h1 className="text-3xl font-bold text-gray-900 mb-2">{playerData.name}</h1>
                    <p className="text-gray-500">{playerData.grade}</p>
                  </div>
                  {playerData.number && (
                    <span className="bg-[#003057] text-white px-4 py-2 rounded-full text-lg font-bold">
                      #{playerData.number}
                    </span>
                  )}
                </div>

                <div className="grid md:grid-cols-2 gap-6">
                  {/* Stats Section */}
                  <div>
                    <h2 className="text-lg font-semibold text-gray-900 mb-4">Player Stats</h2>
                    <div className="space-y-3">
                      <div className="flex justify-between py-2 border-b">
                        <span className="text-gray-600">Jersey Number</span>
                        <span className="font-medium text-gray-900">#{playerData.number || 'N/A'}</span>
                      </div>
                      <div className="flex justify-between py-2 border-b">
                        <span className="text-gray-600">Grade Level</span>
                        <span className="font-medium text-gray-900">{playerData.grade || 'N/A'}</span>
                      </div>
                      {/* Add more stats as needed */}
                    </div>
                  </div>

                  {/* Season Highlights Section */}
                  <div>
                    <h2 className="text-lg font-semibold text-gray-900 mb-4">Personal Statement</h2>
                    <div className="bg-gray-50 rounded-lg p-4">
                      <p className="text-gray-600 text-sm">
                        This is the personal statement for the student athlete. They can talk about their goals, achievements, and aspirations.
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PlayerDetail;